import { FC } from 'react'
import { Button, ButtonProps } from '@lidofinance/lido-ui'

import { useModal } from '../../hooks'
import { WALLET_MODAL } from '../../enums'

export const WalletConnectButton: FC<ButtonProps> = (props) => {
  const { onClick, ...rest } = props
  const { openModal } = useModal(WALLET_MODAL.connect)

  return (
    <Button onClick={openModal} {...rest}>
      Connect wallet
    </Button>
  )
}
