import { FC } from 'react'
import { useEthereumBalance, useSDK } from '@lido-sdk/react'
import { AddressBadge, ButtonProps } from '@lidofinance/lido-ui'
import { FormatToken } from '@lidofinance/eth-ui-primitives'

import { useModal } from '../../hooks'
import { WALLET_MODAL } from '../../enums'

import {
  WalledButtonStyle,
  WalledButtonWrapperStyle,
  WalledButtonBalanceStyle,
  WalledButtonLoaderStyle,
} from './styles'

export const WalletButton: FC<ButtonProps> = (props) => {
  const { onClick, ...rest } = props
  const { openModal } = useModal(WALLET_MODAL.wallet)
  const { account } = useSDK()
  const { data: balance, initialLoading } = useEthereumBalance()

  return (
    <WalledButtonStyle size="sm" variant="text" color="secondary" onClick={openModal} {...rest}>
      <WalledButtonWrapperStyle>
        <WalledButtonBalanceStyle>
          {initialLoading ? (
            <WalledButtonLoaderStyle />
          ) : (
            <FormatToken amount={balance} symbol="ETH" />
          )}
        </WalledButtonBalanceStyle>
        <AddressBadge address={account ?? ''} />
      </WalledButtonWrapperStyle>
    </WalledButtonStyle>
  )
}
