import { useCallback, useContext } from 'react'
import { WalletModalContext } from './context'
import { WALLET_MODAL } from './enums'

type UseModalType = (wModal: WALLET_MODAL) => {
  openModal: () => void
  closeModal: () => void
}

export const useModal: UseModalType = (wModal) => {
  const methods = useContext(WalletModalContext)

  const openModal = useCallback(() => {
    methods.openModal(wModal)
  }, [methods, wModal])

  return {
    openModal,
    closeModal: methods.closeModal,
  }
}
